// @import './reset.scss';

$MainColor: rgba(31, 29, 29, 0.738);

.wow-enter {
    opacity: 0;
    transition: all 1500ms;
}

.wow-enter-active {
    opacity: 1;
    transition: all 1500ms;
}

.red {
    color: $MainColor;
}

@mixin alert-box() {
    background-color: #ffe591;
    padding: 20px;
    border-radius: 5px;
    max-width: 500px;
    width: 100%;
    margin: auto;
}

.my-alert {
    @include alert-box();
}

